import React, { Component } from 'react';
import './App.css';
import SwipeableViews from 'react-swipeable-views';
import Words from './var/words';

import DateView from './DateView';

const StartDate = new Date(2019, 0, 1);

const days = (function () {
	var date = StartDate;
	var now = Date.now();
	var out = [];
	while (date.getTime() < now) {
	// while (out.length < Words.length) {
		out.push({
			date: new Date(date),
			word: Words[out.length]
		});
		date.setDate(date.getDate() + 1);
	}
	return out;
}())

class App extends Component {
	switchDays () {

	}
	render () {
		return (
			<div className="app">
				<SwipeableViews index={ days.length - 1 }>
					{ days.map(props => (<DateView { ...props }/>)) }
					<div className="come-back">Come back tomorrow!</div>
				</SwipeableViews>
			</div>
		);
	}
}
	
export default App;
	