import React, { Component } from 'react';
import Days from './var/days';
import Months from './var/months';
import './DateView.css';

class DateView extends Component {
	constructor (props) {
		super(props);
		console.log('DateView props', props);
	}
	render () {
		var { date, word } = this.props;
		return (
			<div>
				<div className="container">
					<header className="date-header">
						<div className="date-main">
							{ date.getDate() }
						</div>
						<div className="date-extra">
							<div className="date-day">
								{ Days[date.getDay()].label }
							</div>
							<div className="date-month">
								{ Months[date.getMonth()].label }
							</div>
							<div className="date-day">
								{ date.getFullYear() }
							</div>
						</div>
					</header>
					<hr className="divider"/>
					<section className="word">
						<header>{ word.word }</header>
						<div className="word-type">{ word.type }</div>
						<div className="word-def">{ word.defn }</div>
					</section>
					<section className="word-usage">
						<header className="usage-header">USAGE</header>
						<hr className="divider"/>
						{ word.usage }
					</section>
				</div>
			</div>
			);
    }
}
	
export default DateView;
	